<template>
  <div>
    <div class="login-all">
      <Row type="flex" justify="center">
        <Col span="5">
          <div class="inner">
            <div class="login-title">
              <div class="logo">
                <a href="#">
                  <img src="../../assets/5.svg" alt />
                  <span class="title">Ant Design</span>
                </a>
                <div class="subheading">Ant Design 是西湖区最具影响力的 Web 设计规范</div>
              </div>
            </div>
            
            <div class="logoin-form" >
              <Tabs :animated="false">
                <TabPane label="账户密码登陆">
                  <Form>
                    <FormItem prop="name">
                      <Input placeholder="用户名: admin or user" size="large" style="width: 100%">
                        <Icon type="md-person" slot="prefix" />
                      </Input>
                    </FormItem>
                    <FormItem prop="passwd">
                      <Input placeholder="密码: ant.design" size="large" style="width: 100%">
                        <Icon type="md-unlock" slot="prefix" />
                      </Input>
                    </FormItem>
                  </Form>
                </TabPane>
                <TabPane label="手机号登陆">
                  <Form>
                    <FormItem prop="name">
                      <Input placeholder="手机号" size="large" style="width: 100%">
                        <Icon type="ios-phone-portrait" slot="prefix" />
                      </Input>
                    </FormItem>
                    <FormItem prop="passwd">
                      <Input placeholder="验证码" size="large" style="width:60%;margin-right:5px">
                        <Icon type="md-mail" slot="prefix" />
                      </Input>
                      <Button
                        size="large"
                        class="button"
                        v-model="codeMsg"
                        :disabled="codeDisabled"
                        @click="getCode"
                      >{{codeMsg}}</Button>
                    </FormItem>
                  </Form>
                </TabPane>
              </Tabs>
              <div>
                <div style="display:flex;justify-content: space-between">
                  <Checkbox>
                    <span style="padding-left:5px;font-size:14px;color:rgba(0, 0, 0, 0.45)">自动登陆</span>
                  </Checkbox>
                  <a href="#" style="font-size:14px">忘记密码</a>
                </div>
                <div style="padding:20px 0">
                  <Router-link to='/Home'>
                  <Button type="primary" long>登陆</Button>
                  </Router-link>
                </div>
                <div style="display:flex;justify-content: space-between;padding:20px 0">
                  <div>
                    <span style="font-size:14px;margin-right:5px;color:rgba(0, 0, 0, 0.45)">其他登陆方式</span>

                    <svg class="icon" aria-hidden="true">
                      <use xlink:href="#icon-taobao" />
                    </svg>

                    <svg class="icon" aria-hidden="true">
                      <use xlink:href="#icon-weixin" />
                    </svg>

                    <svg class="icon" aria-hidden="true">
                      <use xlink:href="#icon-zhifubao" />
                    </svg>
                  </div>
                  <div>
                    <Router-link to="/Register">
                    <div style="font-size:14px;cursor: pointer;" @click="logoinstate()">注册账户</div>
                    </Router-link>
                  </div>
                </div>
              </div>
            </div>

            

            <div class="footer">
              <div>
                <a href="#" title="Ant Design Pro">Ant Design Pro</a>
                <a href="#" title="github" style="margin:0 30px">
                  <Icon type="logo-github" />
                </a>
                <a href="#" title="Ant Design">Ant Design</a>
              </div>
              <div style="padding:10px 0">Copyrigh &copy; 2019 蚂蚁金服体验技术部出品</div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
import { setInterval, clearInterval } from "timers";
export default {
  data() {
    return {
      logoin:false,
      register:true,
      codeMsg: "获取验证码",
      codeDisabled: false,
      countdown: 60,
      timer: null,
     
    };
  },
  methods: {
    getCode() {
      if (!this.timer) {
        this.timer = setInterval(() => {
          if (this.countdown > 0 && this.countdown <= 60) {
            this.countdown--;
            if (this.countdown !== 0) {
              this.codeMsg = this.countdown + "秒";
              this.codeDisabled = true;
            } else {
              clearInterval(this.timer);
              this.codeMsg = "获取验证码";
              this.countdown = 60;
              this.timer = null;
              this.codeDisabled = false;
            }
          }
        }, 1000);
      }
    },
   
  }
};
</script>

<style >
.login-all {
  background-image: url(../../assets/login.svg);
  background-position: center 110px;
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: #f0f2f5;
}



.login-all .inner {
 
  height: 100vh;
}

.login-all .footer {
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);

}
.login-all .footer a {
  color: rgba(0, 0, 0, 0.45);
}

.login-all .footer a:hover {
  color: rgba(0, 0, 0, 0.65);
}
.login-all .icon {
  width: 2em;
  height: 2em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  margin: 0 7px;
  color: rgba(0, 0, 0, 0.45);
}

.logoin-form .icon:hover {
  color: #2d8cf0;
}

.logoin-form .button {
  width: 38%;
}

.logoin-form .ivu-tabs-bar {
  border-bottom: 1px solid transparent;
}
.logoin-form .ivu-tabs-nav {
  display: table;
  margin: 0 auto;
  float: none;
}

.logoin-form {
  padding: 45px 10px;
}

.login-title {
  padding-top: 70px;
}
.login-title .logo {
  text-align: center;
}
.login-title .logo img {
  height: 44px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

.login-title .logo .title {
  vertical-align: middle;
  color: #333;
  font-weight: bold;
  font-size: 32px;
  margin-left: 5px;
}
.login-title .logo .subheading {
  padding-top: 10px;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.45);
}
</style>
