<template>
  <div>
    <div class="login-all">
      <Row type="flex" justify="center">
        <Col span="5">
          <div class="inner">
            <div class="login-title">
              <div class="logo">
                <a href="#">
                  <img src="../../assets/5.svg" alt />
                  <span class="title">Ant Design</span>
                </a>
                <div class="subheading">Ant Design 是西湖区最具影响力的 Web 设计规范</div>
              </div>
            </div>

            <div class="logoin-register">
              <div style="font-size:16px;padding:25px 0 20px">注册</div>
              <Form ref="registerCustom" :model="registerCustom" :rules="ruleCustom">
                <FormItem prop="mail">
                  <Input
                    placeholder="邮箱"
                    v-model="registerCustom.mail"
                    size="large"
                    style="width: 100%"
                  />
                </FormItem>
                <FormItem prop="password">
                  <Input
                    placeholder="至少六位密码，区分大小写"
                    v-model="registerCustom.password"
                    size="large"
                    style="width: 100%"
                  />
                </FormItem>
                <FormItem prop="passwordCheck">
                  <Input
                    placeholder="确认密码"
                    v-model="registerCustom.passwordCheck"
                    size="large"
                    style="width: 100%"
                  />
                </FormItem>
                <FormItem prop="PhoneNumber">
                  <Input size="large" v-model="registerCustom.PhoneNumber">
                    <Select v-model="PhoneNumbermodel" slot="prepend" style="width: 80px">
                      <Option
                        v-for="item in PhoneNumberlist"
                        :value="item.value"
                        :key="item.value"
                      >{{ item.label }}</Option>
                    </Select>
                  </Input>
                </FormItem>
                <FormItem prop="verificationCode">
                  <Input
                    placeholder="验证码"
                    v-model="registerCustom.verificationCode"
                    size="large"
                    style="width:60%;margin-right:5px"
                  />
                  <Button
                    size="large"
                    style="width:38%"
                    v-model="registercodeMsg"
                    :disabled="registercodeDisabled"
                    @click="registergetCode()"
                  >{{registercodeMsg}}</Button>
                </FormItem>
                <div style="display:flex;justify-content: space-between;font-size:14px">
                  <Button type="primary" style="width:50%;font-size:14px" @click="handeSubmit('registerCustom')">注册</Button>
                  <Router-link to="/LandingPage">
                    <div style="color:#2d8cf0;line-height:28px;cursor: pointer;">使用已有账号登陆</div>
                  </Router-link>
                </div>
              </Form>
            </div>

            <div class="footer">
              <div>
                <a href="#" title="Ant Design Pro">Ant Design Pro</a>
                <a href="#" title="github" style="margin:0 30px">
                  <Icon type="logo-github" />
                </a>
                <a href="#" title="Ant Design">Ant Design</a>
              </div>
              <div style="padding:10px 0">Copyrigh &copy; 2019 蚂蚁金服体验技术部出品</div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
import { setInterval, clearInterval } from "timers";
export default {
  data() {
    const validatepass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码!"));
      } else {
        if (this.registerCustom.passwordCheck !== "") {
          this.$refs.registerCustom.validateField("passwordCheck");
        }
        callback();
      }
    };
    const validatepassCheck = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请确认密码"));
      } else if (value !== this.registerCustom.password) {
        callback(new Error("两次输入的密码不匹配!"));
      } else {
        callback();
      }
    };
    const validatemail = (rules, value, callback) => {
      const mailreg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (value === "") {
        callback(new Error("请输入邮箱地址！"));
      } else if (mailreg.test(value)) {
        callback();
      } else {
        callback(new Error("邮箱地址格式错误！"));
      }
    };
    return {
      registercodeMsg: "获取验证码",
      registercodeDisabled: false,
      registercountdown: 60,
      registertimer: null,
      registerCustom: {
        mail: "",
        password: "",
        passwordCheck: "",
        PhoneNumber: "",
        verificationCode: ""
      },
      ruleCustom: {
        mail: [{ validator: validatemail, trigger: "blur" }],
        password: [{ validator: validatepass, trigger: "blur" }],
        passwordCheck: [{ validator: validatepassCheck, trigger: "blur" }]
      },
      PhoneNumberlist: [
        {
          value: "+86",
          label: "+86"
        },
        {
          value: "+87",
          label: "+87"
        }
      ],
      PhoneNumbermodel: ["+86"]
    };
  },
  methods: {
    registergetCode() {
      if (!this.registertimer) {
        this.timer = setInterval(() => {
          if (this.registercountdown > 0 && this.registercountdown <= 60) {
            this.registercountdown--;
            if (this.registercountdown !== 0) {
              this.registercodeMsg = this.registercountdown + "秒";
              this.registercodeDisabled = true;
            } else {
              clearInterval(this.registertimer);
              this.registercodeMsg = "获取验证码";
              this.registercountdown = 60;
              this.registertimer = null;
              this.registercodeDisabled = false;
            }
          }
        }, 1000);
      }
    },
    handeSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("Success!");
        } else {
          this.$Message.error("Fail!");
        }
      });
    }
  }
};
</script>

<style >
.login-all {
  background-image: url(../../assets/login.svg);
  background-position: center 110px;
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: #f0f2f5;
}

.login-all .inner .logoin-register {
  padding: 25px 10px;
}

.login-all .inner {
  height: 100vh;
}

.login-all .footer {
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
}
.login-all .footer a {
  color: rgba(0, 0, 0, 0.45);
}

.login-all .footer a:hover {
  color: rgba(0, 0, 0, 0.65);
}
.login-all .icon {
  width: 2em;
  height: 2em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  margin: 0 7px;
  color: rgba(0, 0, 0, 0.45);
}

.login-title {
  padding-top: 70px;
}
.login-title .logo {
  text-align: center;
}
.login-title .logo img {
  height: 44px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

.login-title .logo .title {
  vertical-align: middle;
  color: #333;
  font-weight: bold;
  font-size: 32px;
  margin-left: 5px;
}
.login-title .logo .subheading {
  padding-top: 10px;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.45);
}
</style>

